<template>
  <div>
    <div class="nav bg-first d-flex align-items-center justify-content-between">
      <img src="../assets/img/mobile/btn_top_home_n.png" height="64px" class="cursor-pointer" @click="router('Home')" alt="">
      <p class="h5 text-white text-center mb-0">{{pagename}}</p>
      <img src="../assets/img/mobile/btn_top_menu_n.png" height="64px" class="cursor-pointer" @click="menuframe = !menuframe" alt="">
    </div>
    <transition name="fade">
    <div class="menuframe" v-if="menuframe"  @click="close">
        <div class="menu text-center pt-4" >
          <button class="btn text-white py-2 w-100 h5 d-flex align-items-start justify-content-center" v-if="role.includes(9)" @click="router('Store')">
            <img src="../assets/img/mobile/icn_small_shop_n.png" height="25px" class="mr-2" alt="">
            <p class="mb-0">分店資訊</p>
          </button>
          <button class="btn text-white py-2 w-100 h5 d-flex align-items-start justify-content-center" v-if="role.includes(9)" @click="router('Account')">
            <img src="../assets/img/mobile/icn_small_stuffaccount_n.png" height="25px" class="mr-2" alt="">
            <p class="mb-0">員工帳號</p>
          </button>
          <button class="btn text-white py-2 w-100 h5 d-flex align-items-start justify-content-center" v-if="role.includes(9)" @click="router('Report')">
            <img src="../assets/img/mobile/icn_small_statisticalreport_n.png" height="25px" class="mr-2" alt="">
            <p class="mb-0">統計報表</p>
          </button>
          <button class="btn text-white py-2 w-100 h5 d-flex align-items-start justify-content-center" @click="router('Modify')">
            <img src="../assets/img/mobile/icn_small_accountsetting_n.png" height="25px" class="mr-2" alt="">
            <p class="mb-0">帳號設定</p>
          </button>
          <button class="btn text-white py-2 w-100 h5 d-flex align-items-start justify-content-center">
            <img src="../assets/img/mobile/icn_small_contactus_n.png" height="25px" class="mr-2" alt="">
            <p class="mb-0">聯絡我們</p>
          </button>
          <button class="btn text-white py-2 w-100 h5 d-flex align-items-start justify-content-center" @click="logout()">
            <img src="../assets/img/mobile/icn_small_logout_n.png" height="25px" class="mr-2" alt="">
            <p class="mb-0">切換帳號</p>
          </button>
          <div class="mt-4">
            <!-- <p class="text-left text-dark-gray mb-1">登入資訊:</p>
            <p class="text-dark-gray mb-1">{{$cookies.get('merchant')}}({{$cookies.get('merchant_code')}})</p>
            <p class="text-dark-gray mb-1">{{$cookies.get('branch')}}({{$cookies.get('branch_code')}})</p>
            <p class="text-dark-gray mb-1">{{$cookies.get('auth_name')}}({{$cookies.get('auth_acc')}})</p> -->
          </div>
        </div>
    </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Header',
  props: {
    pagename: String
  },
  data () {
    return {
      menuframe: false,
      role: ''
    }
  },
  created () {
    this.role = this.$cookies.get('auth_role')
    // this.role = '99'
  },
  methods: {
    close (e) {
      if (e.target.className.search('menuframe') !== -1) {
        this.menuframe = false
      }
    },
    sendTOParent (type) {
      this.$emit('listenToChildEvent', type)
    },
    router (name) {
      this.menuframe = false
      if (name === 'Modify' && this.$route.name === name) {
        if (this.$route.query.info) {
          this.$router.replace({name: name})
          this.$router.go(this.$router.currentRoute)
        }
      } else if (this.$route.name !== name) {
        this.$router.push({name: name})
      }
    },
    logout () {
      this.$router.push({name: 'Login'})
      this.$cookies.remove('token')
      this.$cookies.remove('auth_role')
    }
  }
}
</script>

<style scoped>
</style>



