<template>
  <div class="footer mb-0 bg-light-gray h5 py-2 text-center">
    <div class="d-flex align-items-center justify-content-center">
      <img src="../assets/img/mobile/footer_logo.png" height="72px" alt="">
      <div class="ml-2">
        <p class="h6 text-center text-gray mb-2 font-weight-bold">點鑽整合行銷股份有限公司</p>
        <p class="h7 text-left text-gray">Tel: 02-2655-8998#702</p>
        <p class="h7 text-left text-gray">E-mail: cs@uupon.tw</p>
      </div>
    </div>
    <p class="text-center text-gray mb-0" style="font-size: 12px;">Copyright © 2022 UUPON Inc. All Rights Reserved.</p>
  </div>
</template>

<script>
export default {
  name: 'Footer',
  mounted () {
    if (document.body.clientHeight - 102 > window.screen.availHeight) {
      document.querySelector('.footer').style.position = 'initial'
    }
    window.onresize = () => {
      return (() => {
        if (document.body.clientHeight - 102 > window.screen.availHeight) {
          document.querySelector('.footer').style.position = 'initial'
        }
      })()
    }
  }
}
</script>
<style scoped>
.footer{
  /* position: absolute; */
  width: 100%;
  bottom: 0;
  letter-spacing: 1px;
}
</style>



