<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <p>4864867</p>
    <div>{{profile}}</div>
    <router-view/>
    <!-- <Footer /> -->
  </div>
</template>

<script>
// import Footer from '@/components/Footer.vue'
export default {
  name: 'App',
  data () {
    return {
      profile: {}
    }
  },
  created () {
  },
  beforeCreate () {
    window.liff.init({
      liffId: '1657816678-RogZdnMQ'
    }).then(() => {
      this.getProfile()
    })
  },
  mounted () {
  },
  methods: {
    getProfile () {
      window.liff.getProfile().then((profile) => {
        this.profile = profile
      })
    }
  }
}
</script>

<style>
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif;
