  margin-top: 60px; */
  /* max-width: 1920px; */
  margin: 0 auto;
  /* padding-top: 50px; */
  padding-top: 64px;
  /* padding-top: 50px; */
  min-width: 320px;
  /* min-height: 100vh; */
  /* min-height: calc(100vh - 50px); */
  background-size: cover;
  background: #f1f1f1;
}
</style>



