import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
// import VueCookies from 'vue-cookies'

Vue.use(Router)
const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: { requiresAuth: true }
    }
  ]
})
